#heroarea {
  .card {
    border: none;
    border-radius: 0;
    .card-img {
      border-radius: 0;
    }
    .card-img-overlay {
      background: rgba(0,0,0,0.15);
      .card-text {
        padding: 0;
        color: $white;
        border-top: 2px solid rgba(0,0,0,0);
        border-bottom: 2px solid rgba(0,0,0,0);
      }
    }
    &:hover {
      overflow: hidden;
      .card-img {
        filter: blur(2px);
      }
      .card-img-overlay {
        background: rgba(255,255,255,0.8);
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: center;
        align-items: center;
        align-content: stretch;
        @include transition(background, .6s);
        .card-text {
          padding: .5rem 0;
          color: $darkgrey;
          border-top: 2px solid $gold;
          border-bottom: 2px solid $gold;
          @include transition(color border, .6s);
        }
      }
    }
  }
}
