@import url('https://fonts.googleapis.com/css?family=Pinyon+Script');
#premierAwardsHeader {
  @include background-cover(top left);
  border-top: 5px solid $faintgold;
  border-bottom: 5px solid $faintgold;
  h1 {
    font-family: 'Pinyon Script', cursive;
    margin-top: .25em;
    font-weight: 600;
    @media (max-width: 767px) {
      font-size: 2em;
    }
    @media (max-width: 376px) {
      font-size: 1.5em;
    }
  }
}
.navbar-brand {
  img {
    @media (max-width: 363px) {
      width: 150px;
      height: 150px;
    }
  }
}

.navbar-toggler {
  &:focus {
    outline: none;
  }
  .fa {
    transition: all 0.2s;
    width: 1em;
  }
  .fa.fa-bars {
    display: none;
  }
  .fa.fa-times {
    display: block;
  }
  &.collapsed {
    .fa.fa-bars {
      display: block;
    }
    .fa.fa-times {
      display: none;
    }
  }
}

.nav-item {
  /*@media (min-width: 992px) {
    margin-right: 2em;
  }*/
  &.active, &:hover {
    .nav-underlined {
      border-bottom: 3px solid $gold;
    }
  }
  .nav-link {
    &.dropdown-toggle::after {
      display: none;
    }
  }
}
