//Boostrap background
$bg: white;

// Our colors
$darkgrey: #212121;
$faintgold: #e2ddc7;
$gold: goldenrod;
$lightgold: #F3E36B;
$white: white;

// Background colors
.darkgrey {
  background-color: $darkgrey;
}
.faintgold {
  background-color: $faintgold;
}
.gold {
  background-color: $gold;
}
.lightgold {
  background-color: $lightgold;
}
.white {
  background-color: $white;
}

// Font colors
.darkgrey-text {
  color: $darkgrey;
}
.faintgold-text {
  color: $faintgold;
}
.gold-text {
  color: $gold;
}
.lightgold-text {
  color: $lightgold;
}
.white-text {
  color: $white;
}
